export default class VelocityComponent {

    constructor() {

        this.class = 'velocity';

        this.accelerationX = 0;
        this.accelerationY = 0;

        this.maxAccelerationX = 3;
        this.maxAccelerationY = 4;

        this.velocityX = 0;
        this.velocityY = 0;
    }
}


